import { CRow, CCol, CButton, CContainer, CHeaderBrand } from '@coreui/react'
import { cilPlus, cilFilter, cilBriefcase } from '@coreui/icons'
import React from 'react'
import CIcon from '@coreui/icons-react'
import './commesse.css'
import { Statusbar } from '../Components/statusbar'

export const Commesse = () => {
  const statusList = [{
    title: "Inserite",
    status: 7,
    color: "one"
  },
  {
    title: "Senza operatori assegnati",
    status: 25,
    color: "two"
  },
  {
    title: "In corso",
    status: 33,
    color: "three"
  },
  {
    title: "Sospese",
    status: 0,
    color: "four"
  },
  {
    title: "Concluse",
    status: 888,
    color: "five"
  },]
  const usersData = [{
    cod: "MN_CTT_981-2020_20233_2071",
    comune: "Pontecorvo",
    init_date: new Date("12-03-2023"),
    fin_date: new Date("27-04-2023"),
    status: "in carico"
  }]

  return (
    <>
      <CRow className="justify-content-between">
        <CCol className='px-0'>
          <CButton shape="rounded-1" style={{color: "white"}} color="info"><CIcon size='lg' icon={cilPlus} />Nuova Commessa</CButton> 
        </CCol>
        <CCol className='d-flex justify-content-end px-0'>
          <CButton shape="rounded-1" style={{color: "rgba(44, 56, 74, 0.7)", backgroundColor: "white", borderColor: "white"}}><CIcon icon={cilFilter} />Filtra per</CButton>
        </CCol>
      </CRow>
      <CRow className="justify-content-between my-2 px-0">
        <CContainer className='py-4 px-4' style={{backgroundColor: "white"}} fluid>
            <CCol className='d-flex justify-content-start'>
             <CHeaderBrand style={{fontSize: "20px"}}><CIcon size='lg' icon={cilBriefcase}/><strong>&nbsp;Commesse</strong></CHeaderBrand>
            </CCol>
            <div className='d-flex justify-content-between my-4'>
              {statusList.map(el =>
                (<Statusbar status={el.status} title={el.title} color={el.color}/>)
              )}
            </div>
            {/* <CSmartTable
              items={usersData}
              columnFilter
              columnSorter
              pagination
              tableProps={{
                hover: true,
              }}
            /> */}
        </CContainer>
      </CRow>
    </>
  )
}
