import {useState} from 'react'
import { CAvatar, CNavbar, CContainer, CNavbarBrand, CNavbarToggler, CNavbarNav, CCollapse, CNavItem, CNavLink, CDropdown, CDropdownToggle, CDropdownMenu, CDropdownItem, CDropdownDivider } from '@coreui/react'
import CIcon from '@coreui/icons-react'; 
import {cilAppsSettings, cilListRich, cilBell, cilEnvelopeOpen, cilSettings} from '@coreui/icons'

export const Navbar = () => {
  const [visible, setVisible] = useState(false)

  return (
    <CNavbar style={{"--cui-navbar-padding-y": "0rem", backgroundColor: "#1D9BEF"}} expand='lg' colorScheme='dark'>
      <CContainer style={{justifyContent: "flex-end"}}>
          <CNavbarNav style={{alignItems: "center"}}>
            <CNavItem>
              <CNavLink href='#'>
                <CIcon icon={cilBell} />
              </CNavLink>
            </CNavItem>
            <CNavItem>
              <CNavLink href='#'>
                <CIcon icon={cilListRich} />
              </CNavLink>
            </CNavItem>
            <CNavItem>
              <CNavLink href='#'>
                <CIcon icon={cilEnvelopeOpen} />
              </CNavLink>
            </CNavItem>
            <CNavItem>
              <CNavLink href='#' active>
                <CAvatar size='md' status="success"> LC </CAvatar>
              </CNavLink>
            </CNavItem>
            <CNavItem>
              <CNavLink href='#'>
                <CIcon icon={cilAppsSettings} />
              </CNavLink>
            </CNavItem>
          </CNavbarNav>
      </CContainer>
    </CNavbar>
  )
}
