import '../styles/side-bar.css'
import {CSidebar, CSidebarBrand, CSidebarNav, CNavItem, CNavGroup, CSidebarToggler} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { cilSpeedometer, cilDescription, cilPeople, cilSettings, cilBriefcase, cilMap, cilBarChart, cilSpreadsheet } from '@coreui/icons'
import { useState } from 'react'
import { NavLink } from 'react-router-dom'
import { useLocation } from 'react-router-dom'
export const SideBar = () => {
    const [sidebarMinimized, setSidebarMinimized] = useState(false)
    const location = useLocation()
    const menuItems = [
        {
            name : "Dashboard",
            icon: cilSpeedometer,
            to: "/"
        },
        {
            name : "Ricerca Sostegno",
            icon: cilDescription,
            to: "/RicercaSostegno"
        },
        {
            name : "Utenti",
            icon: cilPeople,
            to: "/Users"
        },
        {
            name : "Impostazioni",
            icon: cilSettings,
            to: "/Settings"
        },
        {
            name : "Commesse",
            icon: cilBriefcase,
            to: "/Commesse"
        },
        {
            name : "Mappa",
            icon: cilMap,
            to: "/Map"
        },
        {
            name : "Report",
            icon: cilBarChart,
            to: "/Report"
        },
        {
            name : "Log",
            icon: cilSpreadsheet,
            to: "/Log"
        }        
    ]
  return (
    <CSidebar colorscheme='light' className='bg-light' narrow={sidebarMinimized}>
       <CSidebarBrand> {!sidebarMinimized && "Palimanager"} <CSidebarToggler onClick={() => setSidebarMinimized(!sidebarMinimized)} /></CSidebarBrand>
       <CSidebarNav>
        {menuItems.map((item) =>(
            <CNavItem key={item.name}>
                <NavLink className='nav-link' to={item.to} active={location.pathname === item.to}>
                    <CIcon style={{color:"#2C384A"}} size="sm" customClassName="nav-icon" icon={item.icon}/>
                    {!sidebarMinimized && item.name}
                </NavLink>
            </CNavItem>
        ))}
       </CSidebarNav>
    </CSidebar>
  )
}